.app-header {
    background-color: #222;
    height: 36px;
    padding: 8px 32px 8px 16px;
}

.login-button {
    float: right;
}

.app-logo {
    -webkit-animation: app-logo-spin infinite 8s linear;
            animation: app-logo-spin infinite 8s linear;
    height: 35px;
    background: #ff7f7f;
}

.bookshow-button {
    float: right;
    margin-right: 8px;
}

@-webkit-keyframes app-logo-spin {
    from { -webkit-transform: scaleX(-1); transform: scaleX(-1); }
}

@keyframes app-logo-spin {
    from { -webkit-transform: scaleX(-1); transform: scaleX(-1); }
}

.tabs {
    margin-bottom: 20px;
}

.dispNone {
    display: none;
}

.dispBlock {
    display: block;
}

.red {
    color: red;
}


